<template>
  <section>
    <div style="background-color:#fff;font-size:0.8rem;">
      <div style="padding: 0 20px;margin-top:10px;background:rgb(246, 246, 246);border-bottom:1px solid #f6f6f6;display:flex;line-height:40px;">
        <p style="color:#000;width:30%;text-align:left;">当前房型：</p>
        <div style="width:70%;text-align:right;">{{ orderDetail.roomScale.scaleName }}-{{ orderDetail.virtualRoomScale.scaleName }}</div>
      </div>
      <div
        style="margin:0 20px;border-bottom:1px solid #f6f6f6;display:flex;justify-content:space-between;align-items:center;line-height:40px;"
        @click="btnBasic"
      >
        <p style="color:#000;width:30%;text-align:left;">选择基础房型</p>
        <div style="width:70%;text-align:right;">{{ houseType }} ></div>
      </div>
      <div
        style="margin: 0 20px; border-bottom:1px solid #f6f6f6; display:flex; justify-content: space-between;align-items:center; line-height:40px;"
        @click="btnPro"
      >
        <p style="color:#000;width:30%;text-align:left;">选择产品房型</p>
        <div style="width:70%;text-align:right;">{{ pro }} ></div>
      </div>
      <div
        style="margin: 0 20px; border-bottom:1px solid #f6f6f6; display:flex; justify-content: space-between;align-items:center; line-height:40px;"
        @click="btnReason"
      >
        <p style="color:#000;">换房原因</p>
        <span>{{ reason }} ></span>
      </div>
    </div>
    <div
      class="bottom_box"
      @click="btnChang"
    >确认换房</div>
    <!-- 选择基础房型 -->
    <mt-popup
      v-model="isBasic"
      position="bottom"
      style="width:100%;position:fixed;overflow:auto;height:70%;bottom:0;"
    >
      <div>
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">当前可选基础房型</div>
        <div
          class="p"
          @click="chooseBasic(item)"
          v-for="(item,i) in houseData"
          :key="i"
        >{{ item.scaleName }}</div>
      </div>
    </mt-popup>
    <!-- 选择产品房型 -->
    <mt-popup
      v-model="isPro"
      position="bottom"
      style="width:100%;position:fixed;overflow:auto;height:70%;bottom:0;"
    >
      <div>
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">当前可选产品房型</div>
        <div
          class="p"
          @click="choosePro(item)"
          v-for="(item,i) in proList"
          :key="i"
        >{{ item.scaleName }}</div>
      </div>
    </mt-popup>
    <!-- 换房原因 -->
    <mt-popup
      v-model="isReason"
      position="bottom"
      style="width:100%;position:fixed;overflow:auto;height:70%;bottom:0;"
    >
      <div>
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">请选择换房原因</div>
        <div
          class="p"
          v-for="(title,i) in doorlist"
          :key="i"
          @click="chooseReason(title)"
        >{{ title }}</div>
      </div>
    </mt-popup>
  </section>
</template>
<script>
import { Toast } from 'mint-ui';
import { PostBusinessOrderroomRoomscaleChange } from '@/api/api_business_hotel_and_scale';
import { GetBusinessVirtualRoomscaleList, GetBusinessRoomscaleByhotel } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            orderDetail: '',
            reason: '',
            houseType: '',
            pro: '',
            num: '',
            isBasic: false,
            isPro: false,
            isNum: false,
            isReason: false,
            houseData: [],
            proList: [],
            numlist: [],
            doorlist: ['门锁无法打开', '房间设施损耗', '顾客要更换房型', '其他']
        };
    },
    created() {
        this.$store.commit('setHeader', '换房型');
        this.orderDetail = JSON.parse(localStorage.getItem('orderDetail'));
        this.houseType = this.orderDetail.roomScale.scaleName;
        this.pro = this.orderDetail.virtualRoomScale.scaleName;
        this.houseType_id = this.orderDetail.roomScaleId;
        this.pro_id = this.orderDetail.virtualRoomScaleId;
    },
    methods: {
        // 基础房型
        btnBasic() {
            this.isBasic = true;
            GetBusinessRoomscaleByhotel(this.orderDetail.hotelId).done((res) => {
                this.houseData = res.data;
            });
        },
        chooseBasic(item) {
            this.pro = '请选择';
            this.houseType = item.scaleName;
            this.houseType_id = item.id;
            this.isBasic = false;
        },
        // 产品房型
        btnPro() {
            this.isPro = true;
            let param = {
                hotelId: this.orderDetail.hotelId,
                roomScaleId: this.houseType_id
            };
            GetBusinessVirtualRoomscaleList(param).done((res) => {
                this.proList = res.data.filter((item) => item.scaleType == this.orderDetail.virtualRoomScale.scaleType);
            });
        },
        choosePro(item) {
            this.pro = item.scaleName;
            this.pro_id = item.id;
            this.isPro = false;
        },
        // 选择原因
        btnReason() {
            this.isReason = true;
        },
        chooseReason(title) {
            this.reason = title;
            this.isReason = false;
        },
        // 换房
        btnChang() {
            if (this.pro == '请选择') {
                Toast({
                    message: '请选择产品房型',
                    duration: 900
                });
                return false;
            }
            if (this.pro_id == this.orderDetail.virtualRoomScaleId) {
                Toast({
                    message: '请选择目标产品房型',
                    duration: 900
                });
                return false;
            }
            let data = {
                toScaleId: this.houseType_id,
                orderId: this.orderDetail.orderId,
                toVirtualRoomScaleId: this.pro_id,
                reason: this.reason
            };
            PostBusinessOrderroomRoomscaleChange(this.orderDetail.id, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            setTimeout('window.history.go(-1)', 900);
        }
    }
};
</script>
<style scoped>
.p {
    text-align: center;
    line-height: 35px;
    margin: 0 20px;
    border-bottom: 2px solid #f6f6f6;
    font-size: 0.8rem;
    color: #ff7d41;
}
.bottom_box {
    background: #ff7d41;
    color: #fff;
    border-radius: 5px;
    width: 100%;
    line-height: 40px;
    margin: 40px auto;
    font-size: 15px;
}
</style>



